.card {
  height: 250px;
  width: 800px;
  border: 1px solid black;
  .row {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
    .left-column {
      flex: 0 0 39%;
      .left-column__img {
        position: relative;
        width: 100%;
        height: auto; } }
    .right-column {
      padding: 1rem;
      flex: 0 0 50%;
      max-height: 100%;
      text-align: left; } } }
